@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/oswaldregular.woff2) format("woff2"), url(../fonts/oswaldregular.woff) format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: bold;
  font-weight: 500;
  src: url(../fonts/oswaldmedium.woff2) format("woff2"), url(../fonts/oswaldmedium.woff) format("woff");
}
html {
  background-color: #adadad;
}
body {
  background-color: #ffffff;
  width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  position: relative;
}
@media (min-width: 768px) {
  body {
    width: 768px;
  }
}
@media (min-width: 1440px) {
  body {
    width: 1440px;
  }
}
.visyally-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.header {
  padding-top: 10px;
  padding-right: 20px;
  padding-bottom: 17px;
  padding-left: 20px;
